<style scoped>
  .banner-mgt-move {
    cursor: pointer;
    align-items: center;
    display: flex;
    justify-content: center;
  }
  .banner-mgt-move>img {
    height: 22px;
    width: 22px;
  }
  .banner-draggable-header{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .banner-draggable-header>div{
    line-height: 23px;
    color: #222;
    font-weight: bold;
    padding: 8px 10px 8px 10px;
    border-bottom: 1px solid #ebeef5;
  }
  .banner-draggable-width>div:nth-of-type(1){
    width: 6%;
    min-width: 60px;
  }
  .banner-draggable-width>div:nth-of-type(2){
    width: 16%;
    min-width: 160px;
  }
  .banner-draggable-width>div:nth-of-type(3){
    width: 18%;
    min-width: 180px;
  }
  .banner-draggable-width>div:nth-of-type(4){
    width: 10%;
    min-width: 120px;
  }
  .banner-draggable-width>div:nth-of-type(5){
    width: 16%;
    min-width: 160px;
  }
  .banner-draggable-width>div:nth-of-type(6){
    width: 8%;
    min-width: 80px;
  }
  .banner-draggable-width>div:nth-of-type(7){
    width: 26%;
    min-width: 260px;
  }
  .banner-draggable-width>div:nth-of-type(8){
    width: 10%;
    min-width: 120px;
  }
  .banner-draggable-td{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    border-bottom: 1px solid #ebeef5;
  }
  .banner-draggable-td>div{
    padding: 8px 10px 8px 10px;
    /*flex 布局*/
    display: flex;
    /*实现垂直居中*/
    align-items: center;
  }
  .banner-img {
    line-height: 24px !important;
  }
  .banner-img img{
    width: 150px;
    height: 56px;
  }
  .no-data-wrap {
    line-height: 60px;
    text-align: center;
  }
</style>
<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary"  size="mini" icon="el-icon-plus" @click="addBanner">新增 Banner</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <el-form-item label="Banner名称">
            <el-input class="search-input" v-model.trim="searchContent.name" placeholder="请输入名称关键字" size="small" @keyup.enter.native="doSearchHandel" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel" size="mini">搜索</el-button>
          </el-form-item>

          <el-button type="primary" @click="doSortOnceHandel" size="mini" style="float:right;margin-top:26px;" v-if="activeName == 'abled' && !canMove">编辑排序</el-button>
          <el-button type="primary" @click="doSaveSortHandel" :disabled="isDisabled" size="mini" style="float:right;margin-top:26px;" v-else-if="activeName == 'abled'">保存排序</el-button>
        </el-form>
      </div>
    </div>

    <!-- 搜索结果 -->
    <div class="tab-wraper table-wraper" v-loading="loading">
      <el-tabs v-model="activeName">
        <el-tab-pane name="abled">
          <span slot="label">启用 {{this.abledTableData.length}}</span>
        </el-tab-pane>
        <el-tab-pane name="disabled">
          <span slot="label">禁用 {{this.total}}</span>
        </el-tab-pane>

        <div style="background-color: #fff;width: 100;overflow-x: auto;" v-show="activeName == 'abled'">
          <div class="banner-draggable-header banner-draggable-width">
            <div>序号</div>
            <div>名称</div>
            <div>缩略图（750*280）</div>
            <div>跳转类型</div>
            <div>修改时间</div>
            <div>状态</div>
            <div>操作</div>
            <div v-if="canMove"></div>
          </div>
          <!-- 可拖拽的盒子 -->
          <draggable v-model="abledTableData" v-if="canMove">
            <transition-group>
              <div v-for="(item, i) in abledTableData" :key="item.index" class="banner-draggable-td banner-draggable-width">
                <div>{{item.index}}</div>
                <div>{{item.name}}</div>
                <div class="banner-img"><img :src="item.url"></div>
                <div>{{item.typeName}}</div>
                <div>{{item.updateTime}}</div>
                <div>{{item.statusName}}</div>
                <div>
                  <el-button size="mini" @click="editBanner(item)">编辑</el-button>
                  <el-button size="mini" @click="deleteBanner(item)">删除</el-button>
                  <el-button size="mini" @click="disabledBanner(item)">禁用</el-button>
                </div>
                <div class="banner-mgt-move" v-show="canMove">
                  <img src="/shop/static/imgs/move.png" alt="">
                  <span>按住拖拽</span>
                </div>         
              </div>
            </transition-group>
          </draggable>    
          <!-- 不可拖拽的盒子 -->
          <div v-for="(item, i) in abledTableData" :key="item.index" class="banner-draggable-td banner-draggable-width" v-else>
            <div>{{item.index}}</div>
            <div>{{item.name}}</div>
            <div class="banner-img"><img :src="item.url"></div>
            <div>{{item.typeName}}</div>
            <div>{{item.updateTime}}</div>
            <div>{{item.statusName}}</div>
            <div>
              <el-button size="mini" @click="editBanner(item)">编辑</el-button>
              <el-button size="mini" @click="deleteBanner(item)">删除</el-button>
              <el-button size="mini" @click="disabledBanner(item)">禁用</el-button>
            </div>
            <div v-show="canMove"></div>         
          </div>
          <div v-show="abledTableData.length <= 0" class="no-data-wrap">没有查询到您想要的数据</div> 
          
        </div>

        <el-table
          class="fixed-height banner-img"
          :data="disabledTableData"
          empty-text="没有查询到您想要的数据"
          style="width:100%;"
          v-show="activeName == 'disabled'"
          size="small">
          <el-table-column min-width="60" prop="index" label="序号"></el-table-column>
          <el-table-column min-width="150" prop="name" label="名称"></el-table-column>
          <el-table-column width="180" label="缩略图（750*280）">
            <template slot-scope="scope">
              <img :src="scope.row.url">
            </template>
          </el-table-column>
          <el-table-column min-width="120" prop="typeName" label="跳转类型"></el-table-column>
          <el-table-column min-width="160" prop="updateTime" label="修改时间"></el-table-column>
          <el-table-column min-width="80" prop="statusName" label="状态"></el-table-column>
          <el-table-column min-width="320" label="操作">
            <template slot-scope="scope">
              <el-button size="mini" @click="editBanner(scope.row)">编辑</el-button>
              <el-button size="mini" @click="deleteBanner(scope.row)">删除</el-button>
              <el-button size="mini" @click="disabledBanner(scope.row, 'true')">开启</el-button>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页条 -->
        <el-pagination
          v-show="activeName == 'disabled' && disabledTableData.length>0"
          class="tc mt20"
          background
          @size-change="changeSize"
          @current-change="changeCurrent"
          :current-page.sync="currentPage"
          :page-sizes="[20, 50, 100, 150, 200]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </el-tabs>
    </div>
    
    <!-- 删除Bannerdialog -->
    <el-dialog title="删除Banner" :visible.sync="deleteBannerDialogVisible" width="360px">
      <span>您确定要删除所选Banner吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteBannerDialogVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="sureDeleteBanner" size="mini">确 认</el-button>
      </span>
    </el-dialog>
    
    <!-- 启用Bannerdialog -->
    <el-dialog title="启用失败" :visible.sync="statusBannerDialogVisible" width="360px">
      <span>已经超过Banner的启用数量，限制6个</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="statusBannerDialogVisible = false" size="mini">确 认</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import Storage from '@/utils/localStorage'
import Draggable from 'vuedraggable'
import * as utils from '@/utils/utils'
 
const bannerMgtUrl = {
  // 获取Banner列表
  list: '/plateform/a/banner/list',
  // Banner排序
  sort: '/plateform/a/banner/sort',
  // 删除Banner
  del: '/plateform/a/banner/del',
  // 启用禁用Banner
  updateStatus: '/plateform/a/banner/updateStatus'
}
export default {
  data() {
    return {
      loading: false,
      isDisabled: false, // 按钮是否可点击
      canMove: false, // 是否可以拖拽移动
      deleteBannerDialogVisible: false, // 删除
      statusBannerDialogVisible: false, // 启用
      activeName: 'abled', // 启用
      currentPage: 1,
      pageSize: 20,
      total: 0,
      searchContent: { //搜索条件
        name: '', // Banner名字
        status: '' // 状态
      },
      abledTableData: [], // 启用列表
      disabledTableData: [], // 禁用列表
      deleteBannerContent: { // 删除Banner
        id: '',  // ID名
        isDeleted: '' // 删除状态
      },
      allAbledLength: '' // 所有启用banner数量
    }
  },
  created() {
    let activeNameStr = this.$route.query.status
    if(activeNameStr == '0') {
      this.activeName = 'abled'
    }else if(activeNameStr == '1'){
      this.activeName = 'disabled'
    }
  },
  mounted() {
    // 查询。获取全部状态
    this.doSearchHandel(true)
  },
  components: {
    'draggable': Draggable
  },
  methods: {
    /**
     * [changeSize description]
     * @param    {[type]}                 value [description]
     * @return   {[type]}                       [description]
     */
    changeSize (value) {
      this.pageSize = value
      this.doSearchHandel()
    },
    /**
     * [changeCurrent 点击分页条]
     * @param    {[type]}                 value [当前页]
     * @return   {[type]}                       [description]
     */
    changeCurrent (value) {
      this.doSearchHandel(value, 'changeCurrent')
    },
    /**
     * [doSearchHandel 搜索]
     * @return {[type]} [description]
     */
    doSearchHandel(val, str) {
      this.loading = true
      if(str == 'changeCurrent') {
        this.currentPage = val
      }else{
        this.currentPage = 1
      }

      // 搜索启用
      this.searchContent.status = 0

      this.$http.post(bannerMgtUrl.list, { ...this.searchContent, pageNum: 1, pageSize: 400}).then(res => {
        this.loading = false
        if(res.code === 0) {
          if(res.data && res.data.list && res.data.list.length) {
            res.data.list.forEach((item, i) => {
              item.index = i + 1
            })
            
            this.abledTableData = res.data.list
            let length = this.abledTableData.length
            this.allAbledLength = length

          }else {
            this.abledTableData = []
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })

      // 搜索禁用
      this.searchContent.status = 1

      this.$http.post(bannerMgtUrl.list, { ...this.searchContent, pageNum: this.currentPage, pageSize: this.pageSize}).then(res => {
        this.loading = false
        if(res.code === 0) {
          if(res.data && res.data.list && res.data.list.length) {
            res.data.list.forEach((item, i) => {
              item.index = (this.currentPage - 1) * this.pageSize + i + 1
            })
            this.disabledTableData = res.data.list

          }else {
            this.disabledTableData = []
          }

          if(this.currentPage == 1) {
            this.total = res.data.total
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("提示(获取禁用分组列表):", e.message)
      })
    },
    /**
     * [doSortOnceHandel 点击重新排序按钮触发事件]
     * @return {[type]} [description]
     */
    doSortOnceHandel() {
      if(this.abledTableData.length == 0) {
        return
      }
      this.canMove = true
    },
    /**
     * [doSaveSortHandel 点击保存排序按钮触发事件]
     * @return {[type]} [description]
     */
    doSaveSortHandel() {
      if(this.isDisabled === true) {
        return
      }

      let abledTableData = []
      // 组装整洁参数
      this.abledTableData.forEach((item, i) => {
        let itemObj = {
          id: item.id,
          level: i + 1
        }
        abledTableData.push(itemObj)
      })
      // 保存按钮不可再次点击
      this.isDisabled = true
      utils.showLoading()
      this.$http.post(bannerMgtUrl.sort, {bannerLevelList: abledTableData}).then(res => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        utils.clearLoading()
        if(res.code === 0) {
          this.$message.success("重新排序成功")
          this.canMove = false
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        utils.clearLoading()
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [addBanner 新增Banner]
     * @return {[type]} [description]
     */
    addBanner() {
      this.$router.push('/banner_mgt/add_banner')
    },
    /**
     * [editBanner 编辑Banner]
     * @return {[type]} [description]
     */
    editBanner(val) {
      Storage.set('bannerMeg', val)
      this.$router.push('/banner_mgt/edit_banner')
    },
    /**
     * [deleteBanner 删除Banner]
     * @return {[type]} [description]
     */
    deleteBanner(val) {
      this.deleteBannerContent.isDeleted = 1
      this.deleteBannerContent.id = val.id
      this.deleteBannerDialogVisible = true

    },
    /**
     * [sureDeleteBanner 确认删除Banner]
     * @return {[type]} [description]
     */
    sureDeleteBanner() {
      this.$http.post(bannerMgtUrl.del, { ...this.deleteBannerContent}).then(res => {
        if(res.code === 0) {
          this.deleteBannerDialogVisible = false
          this.$message({
            message: '删除Banner成功',
            type: 'success'
          })
          // 查询。获取全部状态
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [disabledBanner 禁用Banner]
     * @return {[type]} [description]
     */
    disabledBanner(val, states) {
      let disableBannerContent
      if(states == 'true') {
        if(this.allAbledLength > 5) {
          this.statusBannerDialogVisible = true
          return
        }
        disableBannerContent = {
          id: val.id,
          status: 0
        }
      }else{
        disableBannerContent = {
          id: val.id,
          status: 1
        }
      }
      this.$http.post(bannerMgtUrl.updateStatus, { ...disableBannerContent}).then(res => {
        if(res.code === 0) {
          if(states == 'true') {
            this.$message({
              message: '开启Banner成功',
              type: 'success'
            })
          }else{
            this.$message({
              message: '禁用Banner成功',
              type: 'success'
            })
          }
          // 查询。获取全部状态
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    }

  }

}
</script>


